        email: Yup.string().email('Elektron poÃ§tunuzu dÃ¼zgÃ¼n daxil edin').required('Elektron poÃ§tunuzu daxil edin'),
        phone:  Yup.string().matches(phoneRegExp, 'Telefon nÃ¶mrÉnizi dÃ¼gÃ¼n daxil edin').required('Telefon nÃ¶mrÉnizi daxil edin'),
        password: Yup.string().required('ÅifrÉnizi  daxil edin'),
        confirmPassword:    Yup.string()
