import React from 'react'

import {Formik , Form , Field, ErrorMessage} from "formik"
import * as Yup from "yup"
import axios from 'axios'

import "../../assets/css/PagesCss/userRegistration.css"
import user from "../../assets/images/page/background/userRegistration.svg"
import Button from '../../components/Button'
import TopSelection from '../../components/TopSelection'
import Cookies from 'js-cookie'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { useState } from 'react'
import { useMediaQuery } from '@material-ui/core'
toast.configure()
function UserRegistration() {
    const controlImg = useMediaQuery('(min-width:1028px)');

    const notify = () => toast.success("HesabÄ±nÄ±z mÃ¼vÉffÉqiyyÉtlÉ yaradÄ±ldÄ±!");
    const notifyW = () => toast.error("Daxil etdiyiniz mÉlumatlarÄ± yanlÄ±ÅdÄ±r!");

    const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/
    const token = Cookies.get("XSRF-TOKEN")
    const headers = {
        'X-CSRF-TOKEN' : token,
        'Content-Type':'multipart/form-data'
    }
    var data = {
        user:""
    }
    const onSubmit =  (values) => {
            const dt = new FormData()
            dt.append('name' , values.name)
            dt.append('email' , values.email)
            dt.append('phone' , values.phone)
            dt.append('password' , values.password)
            dt.append('profilePhoto' , profilePhoto)
            dt.append('profilePhotoName' , profilePhoto?.name)
            axios.post('http://ustatap.testjed.me/public/api/login', dt , headers)
            .then(res => (res.status == 200 && (console.log(res) ,  data.user = res.data  ,localStorage.setItem("LoginUserData" , JSON.stringify(data))  , window.location.href = "/" ) )) 
            .catch(err => console.log(err))
    }
    const [profilePhoto, setprofilePhoto] = useState(null)
    
    const [{alt, src}, setImg] = useState({
        src: "",
        alt: 'Upload an Image'
    });
    
    const ppchanger = (e) => {
        if(e.target.files[0]) {
            document.getElementById('imgPreview').setAttribute('style' , 'height:100px;border:1px solid lightgray;')
            setImg({
                src: URL.createObjectURL(e.target.files[0]),
                alt: e.target.files[0].name
            });    
        }   
        setprofilePhoto(e.target.files[0])
    }

    const initialValues = {
        name:'',
        email:'',
        phone:'',
        password:'',
        confirmPassword:'',
    }
      
    const validationSchema = Yup.object({
        name: Yup.string().required('AdÄ±nÄ±zÄ± daxil edin'),
        email: Yup.string().email('Invalid email format').required('Emailinizi daxil edin'),
        phone:  Yup.string().matches(phoneRegExp, 'Daxil etdiyiniz nÃ¶mrÉ yanlÄ±ÅdÄ±r').required('Telefon nÃ¶mrÉnizi daxil edin'),
        password: Yup.string().required('ÅifrÉni daxil edin'),
        confirmPassword: Yup.string()
        .oneOf([Yup.ref('password'), null], 'ÅifrÉlÉr uyÄun deyil')
    })

    const uploadedImg = {
        backgroundImage: `url(${src})`,
    }

    return (
        <div className="userRegCont">
            <TopSelection  value1="1"/>
            
            <div className="formAndImg">
               {controlImg && <img src={user} width="auto" height="750" alt="" />}
                <Formik initialValues={initialValues} validationSchema={validationSchema} onSubmit={onSubmit} validateOnChange={true} validateOnBlur={false}>
                    <Form action="" method="post" >
                        <Field type="text" placeholder="Ad vÉ Soyad" name="name"/>
                        <div className="errors"><ErrorMessage name="name"/></div>
                        <Field type="mail" placeholder="Elektron poÃ§t Ã¼nvanÄ±" name="email"/>    
                        <div className="errors"><ErrorMessage name="email"/></div>
                        <Field type="text" type="phone" placeholder="Telefon nÃ¶mrÉsi" name="phone"/>
                        <div className="errors" ><ErrorMessage name="phone"/></div>
                        <Field type="password" type="password" placeholder="ÅifrÉ" name="password"/>
                        <div className="errors"><ErrorMessage name="password"/></div>
                        <Field type="password" type="password" placeholder="TÉkrar ÅifrÉ" name="confirmPassword"/>
                        <div className="errors"><ErrorMessage name="confirmPassword"/></div>
                        <button type="button" className="addFile"> <p className="textPhoto">{profilePhoto?.name !== undefined ? profilePhoto.name  : "ÅÉklinizi yÃ¼klÉyin"}</p><input onChange={ppchanger} type="file" className="addFileInput" name="profile" id=""/></button>
                        <div className="btnAndImg"><div className="imgPreview" id="imgPreview" style={uploadedImg}></div>  <Button type="submit" name="Qeydiyatdan keÃ§"/></div>
                    </Form>
                </Formik>

            </div>
            
        </div>
    )
}

export default UserRegistration
